@import url(libs/reset.css);
@import url(libs/fonts.css);
html,
body {
  max-width: 100%;
  height: 100%; }

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  font-size: 16px; }

body {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  font-family: "HelveticaNeueCyr", sans-serif;
  color: #E2DCC8;
  background: #1C1C1C; }

input, button {
  -webkit-appearance: none; }

a {
  text-decoration: none;
  color: #E2DCC8; }
  a:hover {
    color: #B66449; }

ol,
ul,
nav {
  list-style: none; }

img {
  display: block; }

*:focus {
  outline: none; }

*, :after, :before {
  box-sizing: border-box;
  margin: 0; }

.main {
  width: 100%;
  min-height: 100%;
  box-sizing: border-box;
  position: relative; }

.noise {
  z-index: -1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  opacity: 1;
  /*background: #1C1C1C url("../images/noise.png") center center no-repeat;
	background-size: cover;*/ }

.header {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  border-bottom: 1px solid rgba(230, 227, 217, 0.3);
  height: 8.47619vh;
  left: 0;
  position: fixed;
  top: 0;
  z-index: 2; }
  .header__left {
    width: 709px;
    box-sizing: border-box;
    border-right: 1px solid rgba(230, 227, 217, 0.3);
    display: flex;
    flex-shrink: 0; }
  .header__right {
    width: calc(100% - 709px);
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 32px; }
  .header__letter {
    width: 7.80952vh;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 3.61905vh;
    text-align: center;
    flex-shrink: 0;
    box-sizing: border-box;
    border-right: 1px solid rgba(230, 227, 217, 0.3); }
  .header__socials {
    padding: 0 32px;
    display: flex;
    align-items: center; }
    .header__socials a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 1.80952vh;
      height: 1.90476vh; }
      .header__socials a + a {
        margin-left: 32px; }
      .header__socials a svg {
        display: block;
        max-width: 100%;
        max-height: 100%;
        object-fit: contain;
        object-position: center center; }
  .header__menu {
    list-style: none;
    display: flex;
    align-items: center; }
    .header__menu li {
      display: block;
      padding: 0 32px;
      text-transform: uppercase; }
      .header__menu li a {
        font-size: 14px;
        line-height: 140%;
        display: block; }
  .header__phone {
    text-align: right; }
    .header__phone a {
      font-size: 15px;
      line-height: 130%;
      display: block; }

.scroll {
  z-index: 1;
  position: fixed;
  width: 100%; }

.page-title,
.sticky-title {
  overflow: hidden;
  width: 7.80952vh;
  height: 100%;
  position: fixed;
  flex-shrink: 0;
  z-index: 20;
  transform: translateX(-100%);
  border-right: 1px solid rgba(230, 227, 217, 0.3);
  box-sizing: border-box; }
  .page-title.show,
  .sticky-title.show {
    transform: translateX(0); }
  .page-title .number, .page-title .title,
  .sticky-title .number,
  .sticky-title .title {
    display: block;
    transform: translateZ(0); }
  .page-title ul,
  .sticky-title ul {
    text-transform: uppercase;
    width: 100vh;
    font-size: 3.61905vh;
    position: absolute;
    bottom: 0;
    left: 0;
    height: 7.80952vh;
    transform-origin: 0 100%;
    transform: rotate(-90deg) translateY(100%) translateX(8.47619vh);
    white-space: nowrap; }
    .page-title ul li,
    .sticky-title ul li {
      position: absolute;
      padding: 0 12.19048vh 0 3.33333vh;
      overflow: hidden;
      display: flex;
      justify-content: space-between;
      left: 0;
      width: 100%;
      top: 0;
      height: 100%;
      align-items: center; }

.page-title {
  background: #1C1C1C; }

.page-title {
  background: #1C1C1C; }
  .page-title ul li:not(:first-child) {
    transform: translateY(-100%); }

.sticky-title {
  transform: translateX(0) !important;
  letter-spacing: 0.02em;
  border-left: 1px solid rgba(230, 227, 217, 0.3); }
  .sticky-title ul {
    transform: rotate(-90deg) translateY(100%) translateX(0) !important; }
  .sticky-title span {
    display: inline-block; }

.btn {
  border: none;
  color: #E2DCC8;
  cursor: pointer;
  display: inline-block;
  border-radius: 70px;
  background: #B66449;
  transition: 0.3s;
  font-size: 18px;
  line-height: 130%;
  text-transform: uppercase;
  text-align: center;
  padding: 14px 60px; }
  .btn:hover {
    background: #E2DCC8;
    color: #B66449; }
  .btn svg {
    max-width: 36px;
    max-height: 16px;
    display: block;
    object-fit: contain;
    object-position: center; }
  .btn--arrow {
    padding: 26px 36px; }

.h1 {
  font-weight: 500;
  font-size: 76px;
  line-height: 0.85;
  text-transform: uppercase; }

.small {
  font-size: 15px;
  line-height: 130%; }

.home-page {
  height: 100vh;
  top: 0;
  left: 0;
  padding-top: 8.47619vh;
  display: flex; }
  .home-page .scrollable {
    display: flex;
    height: 100%;
    flex-shrink: 0; }
    .home-page .scrollable > * {
      flex-shrink: 0; }
  .home-page section {
    display: flex;
    position: relative;
    height: 100%; }
  .home-page .content {
    width: 100vw;
    height: 100%;
    padding-left: 7.80952vh;
    box-sizing: border-box;
    display: flex; }
    .home-page .content__left {
      width: calc(709px - 7.80952vh);
      border-right: 1px solid rgba(230, 227, 217, 0.3);
      flex-shrink: 0;
      box-sizing: border-box;
      padding: 22px 40px 22px 22px; }
    .home-page .content__right {
      width: calc(100% - (709px - 7.80952vh));
      padding: 22px;
      box-sizing: border-box; }
    .home-page .content__description {
      margin-top: 4.57143vh;
      max-width: 380px; }
      .home-page .content__description + .content__btn-block {
        margin-top: 105px; }
    .home-page .content__btn-block {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-top: 75px; }
    .home-page .content__btn-label {
      margin-right: 27px; }
    .home-page .content__btn-btn {
      flex-shrink: 0; }
